// grab all video-containers
const videoElements = document.querySelectorAll('.video-wrapped');

// loop each video-container
Array.prototype.forEach.call(videoElements, function (elem, i) {

	// clear every video-item inner html
	elem.innerHTML = '';

	// get the text for the play button - defined in data attributes
	const playButtonText = elem.getAttribute('data-embed-play');
	const playButtonTextSubline = elem.getAttribute('data-embed-play-subline');
	const playButtonTextLinkSubline = elem.getAttribute('data-embed-play-subline-link');

	// create a new tag
	const playButtonDIV = document.createElement('div');

	// add a class to the new tag
	playButtonDIV.className = 'video-wrapped-play';

	// add new html code in the new tag
	playButtonDIV.innerHTML = `<div class="play-btn"></div><span class="play-text"> ${playButtonText} </span><span class="play-text is--subline"><a href="${playButtonTextLinkSubline}" target="_blank"> ${playButtonTextSubline} </a></span>`;

	// append the new tag into the parent element
	elem.appendChild(playButtonDIV);

	// onclick event on play-button
	playButtonDIV.addEventListener('click', function (e) {

		if (e.target.tagName.toLowerCase() !== 'a') {

			// get the video-id - defined in a data attribute
			const embedVideoID = elem.getAttribute('data-embed-video-id');

			// get the video library
			const videoLib = elem.getAttribute('data-video-lib');

			// define iframe
			let iframe = '';

			// override iframe variable to generate the iframe code based on the library
			switch (videoLib) {
				
				case 'youtube':
					iframe = `<iframe width="560" height="315" src="https://www.youtube-nocookie.com/embed/${embedVideoID}?autoplay=1&dnt=1" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`;
					break;

				case 'vimeo':
					iframe = `<iframe width="560" height="315" src="https://player.vimeo.com/video/${embedVideoID}?autoplay=1?dnt=1" frameborder="0" allow="autoplay; fullscreen" allowfullscreen></iframe>`;
					break;
			}

			// add the iframe into the parent element
			elem.innerHTML = iframe;
		}
	});

});