$(document).ready(function(){
	
	var $themeSettingsDiv = $('#theme-settings');
	
	// settings
	var $grid = $('#grid-overlay');
	var $grid_show = $('#grid-show');
	
	$('.theme-settings-open-btn').click(function(){
		$themeSettingsDiv.addClass('theme-settings-open');
	});
	
	$('.theme-settings-close-btn').click(function(){
		$themeSettingsDiv.removeClass('theme-settings-open');
	});
	
	$grid_show.change(function() {
		$grid.toggleClass('show');
	});
	
	
});