// init controller
var controller = new ScrollMagic.Controller({globalSceneOptions: {duration: 500}});

// create a scene
new ScrollMagic.Scene({
		duration: 500,	// the scene should last for a scroll distance of 100px
		offset: 500	// start this scene after scrolling for 50px
	})
    .addTo(controller); // assign the scene to the controller
    
	// build scenes
	$(".scroll-section").each(function( index ) {
		index += 1;
		new ScrollMagic.Scene({triggerElement: `#scroll-section-${index}`})
						.setClassToggle(`.image-${index},.content-${index}`, "active") // add class toggle
                    	.addTo(controller);
	});


