$(function() {

    $('.custom-checkbox > .custom-control-input').on('change', evt => {

        var parent = $(evt.target).parent();

        if ($(evt.target).is(":checked")){
            if( parent[0].classList[2] ) {
                $(parent[0].nextElementSibling).removeClass('d-none');
                $(parent[0].nextElementSibling).find('.custom-control-input').attr('required', true);
            }  
        } else {
            if( parent[0].classList[2] ) {
                $(parent[0].nextElementSibling).addClass('d-none');
                $(parent[0].nextElementSibling).find('.custom-control-input').attr('required', false);
            }  
        }

      
    });

    $('.smiley-selector input:radio').on('change', evt => {
        var parent = $(evt.target).parent();
        $(parent).removeClass('selected--nothing');
        $(parent).attr('data-selected-val', $(evt.target).val());
    });

});