$(function() {

    const HERO = $(".module--hero");
    // const HERO_HEIGHT = $(HERO).height();
    const NAVBAR_MAIN = $("#main-nav");
    const BODY = $('body');
    const MENU_TOGGLER = $(".navbar-toggler");
    const CLOSE_MENU = $(".close-menu");
    const LANGUAGE_SWITCHER = $('.fn-language-switch');

    // $(NAVBAR_MAIN).find('ul:first-child').height(HERO_HEIGHT);
    
    $(MENU_TOGGLER).on( "click", function() {
        BODY.addClass("is--menu-visible");
    });

    $(CLOSE_MENU).on( "click", function() {
        BODY.removeClass("is--menu-visible");
        NAVBAR_MAIN.collapse('hide');
    });

    $(LANGUAGE_SWITCHER).on( "click", function() {
        $(this).parent().toggleClass("is--open");
    });

	$(document).on("click", function(e) {
        if ($(e.target).closest('.hero-overlay-contact').length === 0 && $(e.target).closest('.contact-btn').length === 0) {
            $(".list-languages").removeClass("is--open");
        }
    });

    
    window.onscroll = function() {stickyHeader();};

    var header = document.getElementById("site-header-sticky");
    // var sticky = HERO.offsetHeight;
    var sticky = 200;

    function stickyHeader() {
        if (window.pageYOffset > sticky) {
            header.classList.add("is--visible");
        } else {
            header.classList.remove("is--visible");
        }
    }
    

});

